function PlaceCard({ titleOne, titleTwo, imageUrl }) {
  return (
    <div className="place__card--image">
      <div className="card__image">
        <img className="lazy image lazy-loaded" src={imageUrl} alt={titleOne} />
      </div>
      <h3 className="card__title--one">{titleOne}</h3>
      <h4 className="card__title--two">{titleTwo}</h4>
      <span className="card__button--link">
        <img
          className="card__image--car"
          src="https://roadtrippers.com/wp-content/themes/roadtrippers/img/frontpage/icon/greencar2.svg"
          alt=""
        />
        <span className="desktop__text"></span>
        <span className="mobile__text"></span>
      </span>
    </div>
  );
}

function PlaceSlider() {
  const [isMobile, setIsMobile] = useState(false); // Define isMobile first

  useEffect(() => {
    const handleResize = () => {
      const screenWidth = window.innerWidth;

      if (screenWidth >= 768) {
        setIsMobile(false);
      } else {
        setIsMobile(true);
      }
    };

    //   handleResize,       
    handleResize();

    //      
    window.addEventListener('resize', handleResize);

    //      
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const sliderSettings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: isMobile ? 1 : 3,
    slidesToScroll: 1,
    arrows: false,
    responsive: [
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
        },
      },
      {
        breakpoint: 567,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 420,
        settings: {
          slidesToShow: 1,
        },
      },
    ],
  };

  if (isMobile) {
    return (
      <Slider className="place__slider place__margin-top" {...sliderSettings}>
        {cardsData.map((card, index) => (
          <a key={index} href="pay.html" className="place__slide">
            <PlaceCard {...card} />
          </a>
        ))}
      </Slider>
    );
  } else {
    return (
      <div className="place__slider place__margin-top">
        {cardsData.map((card, index) => (
          <a key={index} href="pay.html" className="place__slide">
            <PlaceCard {...card} />
          </a>
        ))}
      </div>
    );
  }
}

export default PlaceSlider;
